export default {
  name: 'Home',
  computed: {
    title() {
      if (this.$store.state.isMobile) {
        return 'Home - Mobile'
      } else if (this.$store.state.isTablet) {
        return 'Home - Tablet'
      } else {
        return 'Home - Desktop'
      }
    },
  },
  mounted() {},
  methods: {
    causeError() {
      this.$log('PLEASE ')
      throw new Error('ERROR')
    },
  },
}
